import React, { ReactElement } from 'react';
import ErrorMessage from '../errorMessage/ErrorMessage';
import { Link } from 'react-router-dom';
import { useAppDispatch, useAppSelector } from '../../helpers/hooks';
import { TransformedBookType } from '../../types/types';
import { FallingLines } from 'react-loader-spinner';

import { fetchMoreBooks, setStartIndex, setBook } from '../../reducers/booksSlice';
import {
    BooksCount,
    BookListContainer,
    BooksList,
    BookItem,
    BookImage,
    BookTitle,
    BookCategory,
    BookAuthors,
    SpinnerWrapper,
    LoadMoreButton,
    ListPlaceholder,
    MoreBooksSpinner,
} from './booksSectionStyles';

export const BooksSection = (): ReactElement => {
    const dispatch = useAppDispatch();
    const booksLoadingStatus = useAppSelector((state) => state.books.booksLoadingStatus);
    const moreBooksLoadingStatus = useAppSelector((state) => state.books.moreBooksLoadingStatus);
    const booksList = useAppSelector((state) => state.books.books);
    const totalItems = useAppSelector((state) => state.books.totalItems);

    const searchValue = useAppSelector((state) => state.books.searchName);
    const sortedBy = useAppSelector((state) => state.books.sortedBy);
    const category = useAppSelector((state) => state.books.category);
    const startIndex = useAppSelector((state) => state.books.startIndex);

    const loadBooks = () => {
        const newStartIndex = startIndex + 30;
        dispatch(setStartIndex(newStartIndex));
        dispatch(fetchMoreBooks({ searchValue, category, sortedBy, newStartIndex }));
    };

    const handleBook = (bookId: string) => {
        localStorage.setItem('bookId', bookId);
        localStorage.setItem('bookList', JSON.stringify(booksList));
        console.log(booksList);
        dispatch(setBook(bookId));
    };

    const renderItems = (array: TransformedBookType[]) => {
        const items = array.map((item: TransformedBookType) => {
            const { id, title, authors, image, categories } = item;
            const category = categories !== undefined ? categories[0] : undefined;
            const authorsString = typeof authors !== 'undefined' ? authors.join(', ') : undefined;

            return (
                <BookItem key={id}>
                    <Link to={`/book-searching/${id}`} onClick={() => handleBook(id)}>
                        <BookImage>
                            <img src={image} alt="book" />
                        </BookImage>
                        <BookTitle>{title}</BookTitle>
                        <BookCategory>{category}</BookCategory>
                        <BookAuthors>{authorsString}</BookAuthors>
                    </Link>
                </BookItem>
            );
        });

        return <BooksList>{items}</BooksList>;
    };

    const books = renderItems(booksList);

    //    ""     
    const errorMessage = booksLoadingStatus === 'error' ? <ErrorMessage /> : null;
    const spinner =
        booksLoadingStatus === 'loading' ? (
            <SpinnerWrapper>
                <FallingLines height="80" width="80" color="#c19a6b" />
            </SpinnerWrapper>
        ) : null;

    //  , ""          .
    const moreBooksErrorMessage = moreBooksLoadingStatus === 'error' ? 'Error! Try Again' : null;
    const moreBooksSpinner = moreBooksLoadingStatus === 'loading' ? <MoreBooksSpinner /> : null;
    const buttonText =
        moreBooksLoadingStatus !== 'error' && moreBooksLoadingStatus !== 'loading' ? 'Load more books' : null;

    return (
        <>
            <BooksCount>
                {totalItems === 0 ? 'Type something in the search box' : `Found ${totalItems} results`}
            </BooksCount>
            <BookListContainer>
                {errorMessage}
                {spinner}
                {booksList.length === 0 && booksLoadingStatus !== 'error' ? (
                    <ListPlaceholder>Here you will see the books list</ListPlaceholder>
                ) : null}
                {books}
            </BookListContainer>
            {booksList.length !== 0 ? (
                <LoadMoreButton onClick={loadBooks}>
                    {moreBooksErrorMessage}
                    {moreBooksSpinner}
                    {buttonText}
                </LoadMoreButton>
            ) : null}
        </>
    );
};
